@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/mixins/box-shadow";
@import "~bootstrap/scss/mixins/buttons";
@import "~bootstrap/scss/mixins/hover";
@import "~bootstrap/scss/mixins/gradients";
@import "~bootstrap/scss/variables";
$bs-height-base: ($line-height-base + $btn-padding-y * 2) !default;
$bs-height-lg : (floor($font-size-lg * $line-height-base) + $btn-padding-y-lg * 2) !default;
$bs-height-sm : (floor($font-size-sm * 1.5) + $btn-padding-y-sm * 2) !default;
$bs-height-xs : (floor($font-size-sm * 1.2) + $btn-padding-y-sm + 1) !default;
.btn-social {
    position: relative;
    padding-left: ($bs-height-base + $btn-padding-x);
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    > :first-child {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        width: $bs-height-base;
        line-height: ($bs-height-base + 2);
        font-size: 1.6em;
        text-align: center;
        border-right: 1px solid rgba(0, 0, 0, 0.2);
    }
    &.btn-lg {
        padding-left: ($bs-height-lg + $btn-padding-x-lg);
        > :first-child {
            line-height: $bs-height-lg;
            width: $bs-height-lg;
            font-size: 1.8em;
        }
    }
    &.btn-sm {
        padding-left: ($bs-height-sm + $btn-padding-x-sm);
        > :first-child {
            line-height: $bs-height-sm;
            width: $bs-height-sm;
            font-size: 1.4em;
        }
    }
    &.btn-xs {
        padding-left: ($bs-height-xs + $btn-padding-x-sm);
        > :first-child {
            line-height: $bs-height-xs;
            width: $bs-height-xs;
            font-size: 1.2em;
        }
    }
}

.btn-social-icon {
    @extend .btn-social;
    height: ($bs-height-base + 2);
    width: ($bs-height-base + 2);
    padding: 0;
    > :first-child {
        border: none;
        text-align: center;
        width: 100%!important;
    }
    &.btn-lg {
        height: $bs-height-lg;
        width: $bs-height-lg;
        padding-left: 0;
        padding-right: 0;
    }
    &.btn-sm {
        height: ($bs-height-sm + 2);
        width: ($bs-height-sm + 2);
        padding-left: 0;
        padding-right: 0;
    }
    &.btn-xs {
        height: ($bs-height-xs + 2);
        width: ($bs-height-xs + 2);
        padding-left: 0;
        padding-right: 0;
    }
}

@mixin btn-social($color-bg, $color: #fff) {
    background-color: $color-bg;
    @include button-variant($color, $color-bg, rgba(0, 0, 0, .2));
}

